.footer {
  background-color: #dcdcdd;
  position: absolute;
  bottom: 0;
  z-index: 100;
}
.footer:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 0%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.footer:before {
    transform: scaleY(0.5);
}
}
.footer:after {
  display: none;
}
.footer ~ .page-content {
  bottom: 2.8rem;
}
.footer {
  bottom: 0;
  width: 100%;
  height: 2.8rem;
  padding: 0;
  z-index: 9000;
  transition-duration: 400ms;
  display: flex;
}
.footer:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: auto;
  right: auto;
  height: 1px;
  width: 100%;
  background-color: #e7e7e7;
  display: block;
  z-index: 1;
  transform-origin: 50% 0%;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2) {
.footer:before {
    transform: scaleY(0.5);
}
}
.footer:after {
  display: none;
}
.footer .footer-item {
  position: relative;
  height: 2.8rem;
  color: #929292;
  text-align: center;
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer .footer-item.active,
.footer .footer-item:active {
  color: #939393;
}
.footer .footer-item .badge {
  position: absolute;
  top: .1rem;
  left: 50%;
  z-index: 100;
  height: .9rem;
  min-width: .9rem;
  padding: 0 .2rem;
  font-size: .6rem;
  line-height: .9rem;
  color: white;
  vertical-align: top;
  background: red;
  border-radius: .5rem;
  margin-left: .1rem;
}
.footer .footer-item .icon {
  top: .1rem;
  width: 1.3rem;
  height: 1.3rem;
  font-size: 1.3rem;
  line-height: 1.3rem;
  padding-top: 0;
  padding-bottom: 0;
  display: inline-block;
  vertical-align: middle;
}
.footer .footer-item .icon ~ label {
  display: block;
  font-size: 0.65rem;
  position: relative;
  top: 0.15rem;
}
.footer .icon {
  font-size: 1.2rem;
  line-height: 1.2rem;
}
.bar input[type="search"] {
  height: 1.45rem;
  margin: 0.3rem 0;
}
body,
input,
select,
textarea {
  font-family: 'futurabookcregular', Arial, Helvetica, sans-serif;
}
@font-face {
  font-family: 'MyWebFont';
  src: url('/assets/fontsfuturabookc-webfont') format('woff2');
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity .3s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: all .3s;
}
.slide-enter {
  opacity: 0;
  transform: translate3d(70%, 0, 0);
}
.slide-leave-active {
  opacity: 0;
  transform: translate3d(-70%, 0, 0);
}
.transition-reverse .slide-enter {
  opacity: 0;
  transform: translate3d(-70%, 0, 0);
}
.transition-reverse .slide-leave-active {
  opacity: 0;
  transform: translate3d(70%, 0, 0);
}
.page-slide {
  height: 100%;
  width: 100%;
}
.content-padding-bottom {
  padding-bottom: 2.8rem;
}
.content-layout {
  width: 80%;
  max-width: 500px;
  margin: 5px auto;
}
.content-relative {
  height: 100%;
  position: relative;
}
.header-shadow {
  background: #FFF;
  -webkit-box-shadow: 0 -3.4px 5px 2px #777;
  -moz-box-shadow: 0 -3.4px 5px 2px #777;
  box-shadow: 0 -3.4px 5px 2px #777;
}
.footer-shadow {
  background: #FFF;
  -webkit-box-shadow: 0 3.4px 5px 2px #777;
  -moz-box-shadow: 0 3.4px 5px 2px #777;
  box-shadow: 0 3.4px 5px 2px #777;
}
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.pageIsLoading {
  width: 100%;
  height: 100px;
}
.max-width {
  max-width: 500px;
  margin: 0 auto;
}



