.footer-secondary {
  padding: 0;
  height: 2.8rem;
  position: absolute;
  width: 100%;
  bottom: 2.8rem;
}
.footer-secondary:after {
  display: none;
}
.footer-secondary ~ .page-content {
  bottom: 5.6rem;
}



